export default {
	data() {
		return {
			text: "个人资料",
			imgs: "",
			url: "",
			type: "",
			uid: "",
			nickname: "",
			icon: "",
			account: ""
		};
	},
	components: {},
	onLoad() {},
	methods: {
		getuser() {
			this.$api.post({
				uid: this.uid
			}, "userInfo").then(res => {
				if (res.result == 0) {
					this.nickname = res.nickname;
					this.icon = res.icon;
					this.account = res.account;
				} else {
					this.$toast(res.resultNote);
				}
			});
		},
		Upfiles() {

		},
		save(url) {
			let parmas = {
				uid: this.uid,
				icon: url
			};
			this.$api
				.post(parmas, "updateIcon")
				.then(res => {
					console.log(res);
					this.$toast(res.resultNote);
					if (res.result == 0) {
						this.getuser();
					}
				})
				.catch(res => {});
		},
		loginOut() {},
		changePwd() {
			this.$router.push({
				path: "/changepsw",
				query: {
					id: this.type
				}
			});
		},
		changeName() {
			let parmas = {
				uid: this.uid,
				nickname: this.nickname
			};
			this.$api
				.post(parmas, "updateNickname")
				.then(res => {
					console.log(res);
					if (res.result == 0) {
						Toast("修改成功!");
					} else {
						Toast(res.resultNote);
					}
				})
				.catch(res => {
					Toast("请求超时!");
				});
		}
	}
};
